import {create} from 'zustand'

export const useUserStore = create((set) => ({
    users: [],//  
    total: 0,
    skip: 0,
    limit: 30,
    searchText: '',//-
    searchedColumn: '',
    columnKey: '',//   
    searchValue: '',//  
    isModalOpen: false,
    selectedUserId: '',
    userInfo: '', //      
    setSearchText: (text) => set({ searchText: text }),
    setSearchedColumn: (column) => set({ searchedColumn: column }),
    setColumnKey: (key) => set({ columnKey: key }), //   
    setSearchValue: (value) => set({ searchValue: value }), //   
    resetFilters: () => set({ columnKey: '', searchValue: '', skip: 0, searchText: '', searchedColumn:''}), //     skip
    openModal: (id) => set({isModalOpen: true, selectedUserId: id}),
    closeModal: () => set({isModalOpen: false, selectedUserId: ''}),
    setUserInfo: (info) => set({userInfo: info}) 
}));